* {
  margin: 0;
  padding: 0;
  list-style: none;
}

.readinglistbody {
  position:absolute;
  top: 300px;
  left: 1000px;
  height: 800px;
  width: 500px;
  position: absolute;
  background-color: #E8F0DB;
  z-index: 999;
}

h1 {
  position:relative;
  margin: 20px auto;
  font-size: 40px;
  text-align: center;
  color: #FF6600;
}

.content {
  position:relative;
  width: 300px;
  height: 300px;
  margin: auto;
}

.box {
  position:relative;
  width: 100%;
  height: 30px;
  background-color: #fff;
  border: 1px solid #FF6600;
}

.box>span {
  position:relative;
  float: left;
  margin-left: 5px;
  font-size: 18px;
  line-height: 30px;
}

.box>input {
  position:relative;
  float: right;
  width: 88%;
  height: 28px;
  border: none;
  outline: none;
}

.box2 {
  position:relative;
  width: 100%;
}

.box2>#list>.item {
  position:relative;
  width: 100%;
  height: 30px;
  color: snow;
  background-color: rgba(248, 165, 42, 0.6);
  border: 1px solid #fff;
}

.box2>ul>.item>.infor {
  position:relative;
  display: inline;
  margin-left: 15px;
  font-size: 18px;
  line-height: 30px;
}

.box2>ul>.item>.del {
  position:relative;
  float: right;
  margin-right: 10px;
  font-size: 20px;
  line-height: 30px;
  user-select: none;
}

.change {
  position:relative;
  display: none;
}

::placeholder{
  position:relative;
color: cyan
}